/**
 * Audit Logs API Route
 * 
 * This endpoint provides secure access to audit logs with proper
 * admin authentication and access controls.
 */

import { NextRequest, NextResponse } from 'next/server'
import { withAuth } from '@/app/lib/auth/auth-middleware'
import { withHighSecurity } from '@/app/lib/security/security-middleware'
import { getAuditLogger, type AuditLogFilter } from '@/app/lib/audit/audit-logger'
import { logger } from '@/app/lib/logger'
import { createError } from '@/app/lib/api-errors'

/**
 * GET /api/admin/audit/logs
 * 
 * Get audit logs with filtering (admin only)
 */
export const GET = withAuth(
  withHighSecurity(async (request: NextRequest, context) => {
    try {
      const { user } = context
      
      // Strict admin check - only RekonnLabs employees
      if (!user.email?.endsWith('@rekonnlabs.com')) {
        await getAuditLogger().logSecurityEvent(
          'ADMIN_ACCESS',
          'error',
          'Unauthorized audit log access attempt',
          user.id,
          { 
            endpoint: '/api/admin/audit/logs',
            userEmail: user.email 
          },
          context.ipAddress,
          context.userAgent
        )

        return NextResponse.json(
          { success: false, error: 'Unauthorized' },
          { status: 403 }
        )
      }

      const { searchParams } = new URL(request.url)
      
      // Parse filter parameters
      const filter: AuditLogFilter = {
        userId: searchParams.get('userId') || undefined,
        action: searchParams.get('action') || undefined,
        resourceType: searchParams.get('resourceType') || undefined,
        severity: searchParams.get('severity') || undefined,
        startDate: searchParams.get('startDate') || undefined,
        endDate: searchParams.get('endDate') || undefined,
        ipAddress: searchParams.get('ipAddress') || undefined,
        limit: Math.min(parseInt(searchParams.get('limit') || '50'), 1000), // Max 1000
        offset: parseInt(searchParams.get('offset') || '0')
      }

      // Validate date range (max 90 days for performance)
      if (filter.startDate && filter.endDate) {
        const start = new Date(filter.startDate)
        const end = new Date(filter.endDate)
        const daysDiff = (end.getTime() - start.getTime()) / (1000 * 60 * 60 * 24)
        
        if (daysDiff > 90) {
          return NextResponse.json(
            { 
              success: false, 
              error: 'Date range too large',
              message: 'Maximum date range is 90 days'
            },
            { status: 400 }
          )
        }
      }

      const auditLogger = getAuditLogger()
      const result = await auditLogger.getAuditLogs(filter)

      // Log admin access to audit logs
      await auditLogger.logAdminAction(
        'ADMIN_ACCESS',
        user.id,
        undefined,
        undefined,
        undefined,
        {
          endpoint: '/api/admin/audit/logs',
          filter,
          resultCount: result.logs.length
        },
        context.ipAddress,
        context.userAgent
      )

      return NextResponse.json({
        success: true,
        data: {
          logs: result.logs.map(log => ({
            id: log.id,
            userId: log.userId,
            action: log.action,
            resourceType: log.resourceType,
            resourceId: log.resourceId,
            severity: log.severity,
            ipAddress: log.ipAddress,
            userAgent: log.userAgent,
            createdAt: log.createdAt,
            // Conditionally include sensitive data
            oldValues: shouldIncludeSensitiveData(log.action) ? log.oldValues : undefined,
            newValues: shouldIncludeSensitiveData(log.action) ? log.newValues : undefined,
            metadata: log.metadata
          })),
          pagination: {
            total: result.totalCount,
            limit: filter.limit || 50,
            offset: filter.offset || 0,
            hasMore: result.hasMore
          },
          filter
        }
      })

    } catch (error) {
      logger.error('Failed to get audit logs', {
        userId: context.user.id,
        error: (error as Error).message
      })

      return NextResponse.json(
        {
          success: false,
          error: 'Failed to get audit logs',
          message: 'Please try again later'
        },
        { status: 500 }
      )
    }
  })
)

/**
 * POST /api/admin/audit/logs/export
 * 
 * Export audit logs for compliance (admin only)
 */
export const POST = withAuth(
  withHighSecurity(async (request: NextRequest, context) => {
    try {
      const { user } = context
      
      // Strict admin check
      if (!user.email?.endsWith('@rekonnlabs.com')) {
        return NextResponse.json(
          { success: false, error: 'Unauthorized' },
          { status: 403 }
        )
      }

      const body = await request.json()
      const { 
        filter = {}, 
        format = 'json',
        includePersonalData = false,
        reason = 'compliance_audit'
      } = body

      // Validate export reason
      const validReasons = [
        'compliance_audit',
        'security_investigation',
        'legal_request',
        'internal_review'
      ]

      if (!validReasons.includes(reason)) {
        return NextResponse.json(
          { 
            success: false, 
            error: 'Invalid export reason',
            message: 'Must provide a valid reason for audit log export'
          },
          { status: 400 }
        )
      }

      // Limit export size for performance
      const exportFilter: AuditLogFilter = {
        ...filter,
        limit: Math.min(filter.limit || 10000, 50000) // Max 50k records
      }

      const auditLogger = getAuditLogger()
      const result = await auditLogger.getAuditLogs(exportFilter)

      // Prepare export data
      const exportData = {
        exportInfo: {
          exportedBy: user.id,
          exportedAt: new Date().toISOString(),
          reason,
          format,
          includePersonalData,
          filter: exportFilter,
          recordCount: result.logs.length
        },
        logs: result.logs.map(log => {
          const exportLog: any = {
            id: log.id,
            action: log.action,
            resourceType: log.resourceType,
            resourceId: log.resourceId,
            severity: log.severity,
            createdAt: log.createdAt,
            metadata: log.metadata
          }

          // Include personal data only if explicitly requested and justified
          if (includePersonalData && (reason === 'legal_request' || reason === 'security_investigation')) {
            exportLog.userId = log.userId
            exportLog.ipAddress = log.ipAddress
            exportLog.userAgent = log.userAgent
            exportLog.oldValues = log.oldValues
            exportLog.newValues = log.newValues
          }

          return exportLog
        })
      }

      // Log the export action
      await auditLogger.logAdminAction(
        'DATA_EXPORT',
        user.id,
        undefined,
        undefined,
        undefined,
        {
          endpoint: '/api/admin/audit/logs/export',
          exportReason: reason,
          recordCount: result.logs.length,
          includePersonalData,
          format
        },
        context.ipAddress,
        context.userAgent
      )

      // Create export file name
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-')
      const fileName = `audit-logs-export-${timestamp}.${format}`

      // Set appropriate headers for file download
      const response = NextResponse.json({
        success: true,
        data: exportData
      })

      response.headers.set('Content-Disposition', `attachment; filename="${fileName}"`)
      response.headers.set('Content-Type', format === 'json' ? 'application/json' : 'text/csv')

      return response

    } catch (error) {
      logger.error('Failed to export audit logs', {
        userId: context.user.id,
        error: (error as Error).message
      })

      return NextResponse.json(
        {
          success: false,
          error: 'Failed to export audit logs',
          message: 'Please try again later'
        },
        { status: 500 }
      )
    }
  })
)

/**
 * DELETE /api/admin/audit/logs/cleanup
 * 
 * Clean up old audit logs (admin only)
 */
export const DELETE = withAuth(
  withHighSecurity(async (request: NextRequest, context) => {
    try {
      const { user } = context
      
      // Strict admin check
      if (!user.email?.endsWith('@rekonnlabs.com')) {
        return NextResponse.json(
          { success: false, error: 'Unauthorized' },
          { status: 403 }
        )
      }

      const { searchParams } = new URL(request.url)
      const retentionDays = parseInt(searchParams.get('retentionDays') || '365')

      // Validate retention period (minimum 90 days for compliance)
      if (retentionDays < 90) {
        return NextResponse.json(
          {
            success: false,
            error: 'Invalid retention period',
            message: 'Minimum retention period is 90 days'
          },
          { status: 400 }
        )
      }

      const auditLogger = getAuditLogger()
      
      // Perform cleanup
      await auditLogger.cleanupOldAuditLogs(retentionDays)

      // Log the cleanup action
      await auditLogger.logAdminAction(
        'SYSTEM_ERROR', // Using existing action, could add AUDIT_CLEANUP
        user.id,
        undefined,
        undefined,
        undefined,
        {
          endpoint: '/api/admin/audit/logs/cleanup',
          retentionDays,
          action: 'audit_log_cleanup'
        },
        context.ipAddress,
        context.userAgent
      )

      return NextResponse.json({
        success: true,
        message: 'Audit log cleanup completed successfully',
        data: {
          retentionDays,
          cleanupDate: new Date().toISOString()
        }
      })

    } catch (error) {
      logger.error('Failed to cleanup audit logs', {
        userId: context.user.id,
        error: (error as Error).message
      })

      return NextResponse.json(
        {
          success: false,
          error: 'Failed to cleanup audit logs',
          message: 'Please try again later'
        },
        { status: 500 }
      )
    }
  })
)

/**
 * Determine if sensitive data should be included based on action type
 */
function shouldIncludeSensitiveData(action: string): boolean {
  const sensitiveActions = [
    'USER_CREATED',
    'USER_UPDATED', 
    'USER_DELETED',
    'OAUTH_TOKEN_CREATED',
    'OAUTH_TOKEN_UPDATED',
    'TIER_UPGRADE',
    'TIER_DOWNGRADE'
  ]

  return !sensitiveActions.includes(action)
}